@import 'variables';
@import 'mixins';

nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: $darksecondary-color;
  color: $lightneutral-color;
  z-index: 999;
  padding: .5rem;

  @media(min-width: 768px) {
    padding: .5rem 1rem; }


  ul {
    margin-bottom: 0;
    background-color: transparent;
    width: auto;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);

    @media(min-width: 1024px) {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-around;
      align-items: center; }

    li {
      display: block;
      margin: 0 1rem;
      text-align: center;

      a {
        @include skew-text;
        text-decoration: none;
        display: inline-block;
        font-family: 'Comic Neue', cursive;
        letter-spacing: -1px;
        text-transform: uppercase;
        color: $lightneutral-color;
        text-shadow: 1px 1px $darkneutral-color;

        &:hover {
          color: $lightprimary-color; }

        @media(min-width: 768px) {
          font-size: 1.5rem; } } } } }
