@import 'variables';
@import 'mixins';

article#about {
  padding-top: 2rem;

  section.container {
    &::first-of-type {
      margin-bottom: 12.5vh; }

    blockquote {
      @include comic-card;
      position: relative;
      padding: 1.5rem;
      margin: 2rem 0;

      @media(min-width: 480px) {
        padding: 2rem;
        margin: 2rem 1rem; }

      p {
        strong {
          @include blammo;
          position: absolute;
          top: -2rem;
          left: -2rem;
          font-size: 2rem; } } }

    &::after {
      display: none; }

    p:last-child {
      margin-bottom: 0; }

    ul {
      background-color: transparent;
      display: flex;
      flex-flow: column nowrap;
      list-style-type: none;
      padding: 0;
      width: 100%;
      margin: 1rem 0;

      @media (min-width: 480px) {
        display: grid;
        gap: .5rem;
        grid-template-columns: repeat(auto-fit, minmax(calc(25% - 0.25rem), 1fr)); }

      li {
        @include comic-card;
        padding: 0.25rem 1rem;
        display: flex;
        align-items: center;
        word-break: break-word; } } }


  @media(min-width: 768px) {
    section.container:nth-child(1) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, auto);
      column-gap: 1rem;

      h2, blockquote {
        grid-column: 1 / 3; }

      p:nth-child(n+5) {
        grid-column: 1 / 3;
        column-count: 2; } } } }
