@import 'variables';
@import 'mixins';

article footer#files {
  background-color: transparent;
  color: inherit;
  padding: 0;

  div.container {
    position: relative;
    text-align: center;
    margin: 1rem auto;
    max-width: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;

    @media (min-width: 480px) {
      display: grid;
      grid-template-columns: repeat(2, 50%);
      grid-template-rows: repeat(3, auto);
      gap: 1rem; }

    h3 {
      text-align: center;
      font-size: 2.5rem;

      @media (min-width:480px) {
        text-align: left;
        transform: translateX(1rem);
        margin: 2rem auto 0; } }

    a {
      margin: 1rem auto;
      height: 10rem;
      width: calc(100% - 3rem);
      max-width: 100%;

      @media (min-width: 480px) {
        width: calc(100% - 2rem); }

      &:nth-of-type(1),
      &:nth-of-type(2) {
        @include comic-border;
        @include blue-block;
        font-size: 2rem;

        @media (min-width: 480px) {
          grid-row: 2; } }

      &:nth-of-type(3) {
        @include button;
        grid-column: 1 / 3;
        margin: 1rem auto 0; }

      @media(min-width: 480px) {
        width: 100%;
        grid-row: 3; }

      &::after {
        content: none; } } } }
