@import 'variables';
@import 'mixins';

footer.page-footer {
  background-color: $darksecondary-color;
  color: $lightneutral-color;
  padding: 2rem 1rem 7rem;
  text-align: center;
  box-shadow: 0 0 .5rem $darksecondary-color;

  @media (min-width: 768px) {
    padding-bottom: 3rem; }

  a {
    color: $lightneutral-color; }

  p {
    max-width: 600px;
    margin: 0 auto 2rem;

    a {
      border-bottom: dashed 1px $lightneutral-color; } }

  .container ul {
    display: flex;
    flex-flow: column nowrap;
    background-color: transparent;
    margin: 2rem auto;
    padding: 0;
    max-width: 100%;

    @media (min-width: 480px) {
      max-width: 600px;
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, calc(50% - 1rem)));
      gap: 1rem; }

    li a {
      @include button;
      height: auto;
      font-size: 1rem;
      width: 16rem;
      margin: 0 auto;

      svg {
        fill: $black;
        margin-right: .25rem; } } } }
