@import 'variables';
@import 'mixins';

aside#styles {
  display: block;
  width: 100%;
  background-color: $secondary-color;
  color: $lightneutral-color;

  div.container {
    width: calc(100% - 2rem);
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem 0;
    text-align: center;

    @media(min-width: 768px) {
      display: grid;
      grid-template-rows: repeat(2, 1fr);
      grid-template-columns: repeat(2, 1fr);
      row-gap: 2rem; } }

  h2 {
    @include blammo;
    transform: rotate(0);
    text-align: center;
    width: fit-content;
    padding: 1rem 1.5rem 1rem 1rem;

    @media(min-width: 768px) {
      padding: 0;
      justify-self: center;
      align-self: end;
      transform: rotate(6deg); } }

  .container ul.features {
    display: flex;
    flex-flow: column nowrap;
    background-color: transparent;
    padding: 0;

    @media(min-width: 480px) {
      grid-area: 1 / 1 / 3 / 2; }

    li {
      padding: 1rem;
      margin: 0 auto;
      color: $lightneutral-color;

      @media(min-width: 480px) {
 }        // margin: 1rem auto;

      span {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;

        a {
          @include blue-block;
          flex-grow: 1;
          margin-bottom: 1rem; } } } }

  a.link-allstyles {
    @include button;

    @media(min-width: 768px) {
      justify-self: center;
      align-self: start;
      transform: rotate(6deg);

      &:hover {
        transform: scale(1.1) rotate(6deg); } } } }
